/**
 * API Client
 * Centralized API communication with the backend
 */

const API_BASE_URL =
  (process.env.NEXT_PUBLIC_API_URL || "http://localhost:3001") + "/api";

// Helper function to get auth token from Redux persisted state
function getAuthToken(): string | null {
  if (typeof window === "undefined") return null;
  
  try {
    // Get from redux-persist store
    const persistedState = localStorage.getItem("persist:timeless-root");
    if (persistedState) {
      const parsed = JSON.parse(persistedState);
      if (parsed.auth) {
        const authState = JSON.parse(parsed.auth);
        return authState.token;
      }
    }
  } catch (error) {
    console.error("Failed to get auth token:", error);
  }
  
  return null;
}

// Helper function to set auth token (deprecated - use Redux dispatch instead)
export function setAuthToken(token: string) {
  // This is kept for backward compatibility but Redux should handle persistence
  console.warn("setAuthToken is deprecated. Use Redux dispatch(loginSuccess) instead.");
}

// Helper function to remove auth token (deprecated - use Redux dispatch instead)
export function removeAuthToken() {
  // This is kept for backward compatibility but Redux should handle persistence
  console.warn("removeAuthToken is deprecated. Use Redux dispatch(logout) instead.");
}

// Base fetch function with auth headers
async function apiFetch(endpoint: string, options: RequestInit = {}) {
  const token = getAuthToken();

  const headers: Record<string, string> = {
    "Content-Type": "application/json",
    ...(options.headers as Record<string, string>),
  };

  if (token) {
    headers["Authorization"] = `Bearer ${token}`;
  }

  const response = await fetch(`${API_BASE_URL}${endpoint}`, {
    ...options,
    headers,
  });

  if (!response.ok) {
    const error = await response
      .json()
      .catch(() => ({ message: "Network error" }));
    throw new Error(error.message || "API request failed");
  }

  return response.json();
}

// Auth API
export const authAPI = {
  register: async (userData: {
    email: string;
    password: string;
    firstName: string;
    lastName: string;
  }) => {
    const response = await apiFetch("/auth/register", {
      method: "POST",
      body: JSON.stringify(userData),
    });
    if (response.data.tokens.accessToken) {
      setAuthToken(response.data.tokens.accessToken);
    }
    return response.data;
  },

  login: async (credentials: { email: string; password: string }) => {
    const response = await apiFetch("/auth/login", {
      method: "POST",
      body: JSON.stringify(credentials),
    });
    if (response.data.tokens.accessToken) {
      setAuthToken(response.data.tokens.accessToken);
    }
    return response.data;
  },

  logout: async () => {
    await apiFetch("/auth/logout", { method: "POST" });
    removeAuthToken();
  },

  getCurrentUser: async () => {
    const response = await apiFetch("/auth/me");
    return response.data.user;
  },

  refreshToken: async (refreshToken: string) => {
    const response = await apiFetch("/auth/refresh", {
      method: "POST",
      body: JSON.stringify({ refreshToken }),
    });
    if (response.data.accessToken) {
      setAuthToken(response.data.accessToken);
    }
    return response.data;
  },

  
  verifyEmail: async (token: string) => {
    const response = await apiFetch("/auth/verify-email", {
      method: "POST",
      body: JSON.stringify({ token }),
    });
    return response;
  },

  resendVerification: async () => {
    const response = await apiFetch("/auth/resend-verification", {
      method: "POST",
    });
    return response;
  },
};

// Products API
export const productsAPI = {
  getAll: async (params?: any) => {
    const queryString = params ? `?${new URLSearchParams(params)}` : "";
    const response = await apiFetch(`/products${queryString}`);
    return response.data;
  },

  getById: async (id: string) => {
    const response = await apiFetch(`/products/${id}`);
    return response.data.product;
  },

  getFeatured: async () => {
    const response = await apiFetch("/products/featured");
    return response.data.products;
  },
};

// Cart API
export const cartAPI = {
  get: async () => {
    const response = await apiFetch("/cart");
    return response.data;
  },

  add: async (productId: string, quantity: number = 1, variantId?: string) => {
    const response = await apiFetch("/cart", {
      method: "POST",
      body: JSON.stringify({ productId, quantity, variantId }),
    });
    return response.data;
  },

  update: async (itemId: string, quantity: number) => {
    const response = await apiFetch(`/cart/${itemId}`, {
      method: "PUT",
      body: JSON.stringify({ quantity }),
    });
    return response.data;
  },

  remove: async (itemId: string) => {
    await apiFetch(`/cart/${itemId}`, { method: "DELETE" });
  },

  clear: async () => {
    await apiFetch("/cart", { method: "DELETE" });
  },
};

